'use client';

import { useState, useEffect } from 'react';
import { 
  PARAMETERS, 
  FEATURE_ORDER, 
  ERAS, 
  ERA_DESCRIPTIONS,
  PRESETS, 
  DEFAULT_CONFIG,
  CATEGORY_COLORS 
} from '@/components/constants';
import { 
  findSimilarPolities, 
  compareEras, 
  getRiskAssessment,
  formatDuration,
  formatYear 
} from '@/components/similarity';

export default function PredictPage() {
  const [config, setConfig] = useState(DEFAULT_CONFIG);
  const [selectedEra, setSelectedEra] = useState(null);
  const [polities, setPolities] = useState([]);
  const [scalerParams, setScalerParams] = useState(null);
  const [results, setResults] = useState(null);
  const [eraComparison, setEraComparison] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [activePreset, setActivePreset] = useState(null);

  // Load data on mount
  useEffect(() => {
    async function loadData() {
      try {
        const [politiesRes, scalerRes] = await Promise.all([
          fetch('/data/polities.json'),
          fetch('/data/scaler-params.json')
        ]);
        
        const politiesData = await politiesRes.json();
        const scalerData = await scalerRes.json();
        
        setPolities(politiesData);
        setScalerParams(scalerData);
        setIsLoading(false);
      } catch (error) {
        console.error('Failed to load data:', error);
        setIsLoading(false);
      }
    }
    loadData();
  }, []);

  // Compute results when config or era changes
  useEffect(() => {
    if (!polities.length || !scalerParams) return;

    const similarityResults = findSimilarPolities(
      config, 
      polities, 
      scalerParams, 
      { k: 5, era: selectedEra, maxDuration: 1000 }
    );
    
    const eraResults = compareEras(config, polities, scalerParams, { k: 5, maxDuration: 1000 });
    
    setResults(similarityResults);
    setEraComparison(eraResults);
  }, [config, selectedEra, polities, scalerParams]);

  const handleParamChange = (key, value) => {
    setConfig(prev => ({ ...prev, [key]: parseFloat(value) }));
    setActivePreset(null);
  };

  const handlePresetSelect = (presetName) => {
    setConfig(PRESETS[presetName]);
    setActivePreset(presetName);
  };

  const risk = results ? getRiskAssessment(results.durationEstimate) : null;

  if (isLoading) {
    return (
      <div className="min-h-screen bg-[#0a0a0a] flex items-center justify-center">
        <div className="text-gray-400">Loading historical data...</div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-[#0a0a0a] text-gray-100">
      {/* Header */}
      <header className="border-b border-gray-800 bg-[#0a0a0a]/95 backdrop-blur sticky top-0 z-10">
        <div className="max-w-6xl mx-auto px-6 py-4">
          <div className="flex items-center justify-between">
            <div>
              <a href="/" className="text-xl font-semibold tracking-tight text-white hover:text-gray-300 transition">
                PsychohistoryML
              </a>
              <span className="ml-3 text-sm text-gray-500">/ Simulator</span>
            </div>
            <nav className="flex gap-6 text-sm">
              <a href="/research" className="text-gray-400 hover:text-white transition">Research</a>
              <a href="/predict/methodology" className="text-gray-400 hover:text-white transition">Methodology</a>
            </nav>
          </div>
        </div>
      </header>

      <main className="max-w-6xl mx-auto px-6 py-8">
        {/* Title */}
        <div className="mb-8">
          <h1 className="text-3xl font-bold mb-2">Polity Simulator</h1>
          <p className="text-gray-400">
            Configure a society and find historically similar polities from the Seshat database.
          </p>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* Left Column: Controls */}
          <div className="lg:col-span-2 space-y-6">
            
            {/* Presets */}
            <div className="bg-gray-900/50 rounded-lg p-4 border border-gray-800">
              <h3 className="text-sm font-medium text-gray-400 mb-3">Quick Presets</h3>
              <div className="flex flex-wrap gap-2">
                {Object.keys(PRESETS).map(preset => (
                  <button
                    key={preset}
                    onClick={() => handlePresetSelect(preset)}
                    className={`px-3 py-1.5 rounded text-sm transition ${
                      activePreset === preset
                        ? 'bg-blue-600 text-white'
                        : 'bg-gray-800 text-gray-300 hover:bg-gray-700'
                    }`}
                  >
                    {preset}
                  </button>
                ))}
              </div>
            </div>

            {/* Parameter Sliders */}
            <div className="bg-gray-900/50 rounded-lg p-6 border border-gray-800">
              <h3 className="text-sm font-medium text-gray-400 mb-4">Configure Your Polity</h3>
              
              {/* Complexity */}
              <div className="mb-6">
                <h4 className="text-xs uppercase tracking-wider text-blue-400 mb-3 flex items-center gap-2">
                  <span className="w-2 h-2 rounded-full bg-blue-400"></span>
                  Complexity
                </h4>
                <div className="space-y-4">
                  {Object.entries(PARAMETERS)
                    .filter(([_, p]) => p.category === 'complexity')
                    .map(([key, param]) => (
                      <ParameterSlider
                        key={key}
                        paramKey={key}
                        param={param}
                        value={config[key]}
                        onChange={handleParamChange}
                      />
                    ))}
                </div>
              </div>

              {/* Warfare */}
              <div className="mb-6">
                <h4 className="text-xs uppercase tracking-wider text-red-400 mb-3 flex items-center gap-2">
                  <span className="w-2 h-2 rounded-full bg-red-400"></span>
                  Warfare
                </h4>
                <div className="space-y-4">
                  {Object.entries(PARAMETERS)
                    .filter(([_, p]) => p.category === 'warfare')
                    .map(([key, param]) => (
                      <ParameterSlider
                        key={key}
                        paramKey={key}
                        param={param}
                        value={config[key]}
                        onChange={handleParamChange}
                      />
                    ))}
                </div>
              </div>

              {/* Religion */}
              <div>
                <h4 className="text-xs uppercase tracking-wider text-amber-400 mb-3 flex items-center gap-2">
                  <span className="w-2 h-2 rounded-full bg-amber-400"></span>
                  Religion
                </h4>
                <div className="space-y-4">
                  {Object.entries(PARAMETERS)
                    .filter(([_, p]) => p.category === 'religion')
                    .map(([key, param]) => (
                      <ParameterSlider
                        key={key}
                        paramKey={key}
                        param={param}
                        value={config[key]}
                        onChange={handleParamChange}
                      />
                    ))}
                </div>
              </div>
            </div>

            {/* Era Filter */}
            <div className="bg-gray-900/50 rounded-lg p-4 border border-gray-800">
              <h3 className="text-sm font-medium text-gray-400 mb-3">Filter by Era (optional)</h3>
              <div className="flex flex-wrap gap-2">
                <button
                  onClick={() => setSelectedEra(null)}
                  className={`px-3 py-1.5 rounded text-sm transition ${
                    selectedEra === null
                      ? 'bg-gray-600 text-white'
                      : 'bg-gray-800 text-gray-400 hover:bg-gray-700'
                  }`}
                >
                  All Eras
                </button>
                {ERAS.map(era => (
                  <button
                    key={era}
                    onClick={() => setSelectedEra(era)}
                    className={`px-3 py-1.5 rounded text-sm transition ${
                      selectedEra === era
                        ? 'bg-gray-600 text-white'
                        : 'bg-gray-800 text-gray-400 hover:bg-gray-700'
                    }`}
                  >
                    {era}
                  </button>
                ))}
              </div>
              {selectedEra && (
                <p className="mt-2 text-xs text-gray-500">{ERA_DESCRIPTIONS[selectedEra]}</p>
              )}
            </div>
          </div>

          {/* Right Column: Results */}
          <div className="space-y-6">
            
            {/* Duration Estimate */}
            {results && (
              <div className="bg-gray-900/50 rounded-lg p-6 border border-gray-800">
                <h3 className="text-sm font-medium text-gray-400 mb-4">Duration Estimate</h3>
                
                <div className="text-center mb-4">
                  <div className="text-5xl font-bold" style={{ color: risk?.color }}>
                    {results.durationEstimate ? `~${results.durationEstimate}` : '—'}
                  </div>
                  <div className="text-gray-400 mt-1">years</div>
                </div>

                {risk && (
                  <div 
                    className="text-center text-sm px-3 py-1.5 rounded-full mx-auto w-fit"
                    style={{ 
                      backgroundColor: `${risk.color}20`,
                      color: risk.color
                    }}
                  >
                    {risk.description}
                  </div>
                )}

                {results.durationRange[0] !== null && (
                  <div className="mt-4 text-center text-sm text-gray-500">
                    Range: {results.durationRange[0]} – {results.durationRange[1]} years
                  </div>
                )}

                <div className="mt-4 pt-4 border-t border-gray-800">
                  <div className="flex justify-between text-xs text-gray-500">
                    <span>Confidence</span>
                    <span>{(results.confidence * 100).toFixed(0)}%</span>
                  </div>
                  <div className="mt-1 h-1.5 bg-gray-800 rounded-full overflow-hidden">
                    <div 
                      className="h-full bg-blue-500 rounded-full transition-all duration-300"
                      style={{ width: `${results.confidence * 100}%` }}
                    />
                  </div>
                </div>
              </div>
            )}

            {/* Similar Polities */}
            {results && results.similar.length > 0 && (
              <div className="bg-gray-900/50 rounded-lg p-6 border border-gray-800">
                <h3 className="text-sm font-medium text-gray-400 mb-4">
                  Similar Historical Polities
                </h3>
                <div className="space-y-3">
                  {results.similar.map((polity, i) => (
                    <div 
                      key={i}
                      className="p-3 bg-gray-800/50 rounded-lg hover:bg-gray-800 transition cursor-pointer"
                    >
                      <div className="flex justify-between items-start">
                        <div>
                          <div className="font-medium text-white">{polity.name}</div>
                          <div className="text-xs text-gray-500 mt-0.5">
                            {polity.era} · {formatYear(polity.start)} – {formatYear(polity.end)}
                          </div>
                        </div>
                        <div className="text-right">
                          <div className="text-sm font-medium text-blue-400">
                            {polity.similarity}%
                          </div>
                          <div className="text-xs text-gray-500">
                            {polity.duration} yrs
                          </div>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
                <div className="mt-3 text-xs text-gray-600 text-center">
                  Based on {results.candidateCount} polities in database
                </div>
              </div>
            )}

            {/* Era Comparison */}
            {eraComparison.length > 0 && !selectedEra && (
              <div className="bg-gray-900/50 rounded-lg p-6 border border-gray-800">
                <h3 className="text-sm font-medium text-gray-400 mb-4">
                  Same Config, Different Eras
                </h3>
                <div className="space-y-2">
                  {eraComparison.map(era => (
                    <div 
                      key={era.era}
                      className="flex justify-between items-center py-2 border-b border-gray-800 last:border-0"
                    >
                      <span className="text-gray-300">{era.era}</span>
                      <div className="text-right">
                        <span className="font-medium text-white">
                          {era.durationEstimate ? `${era.durationEstimate} yrs` : '—'}
                        </span>
                        {era.topMatch && (
                          <div className="text-xs text-gray-500 truncate max-w-[120px]">
                            e.g. {era.topMatch.name}
                          </div>
                        )}
                      </div>
                    </div>
                  ))}
                </div>
                <p className="mt-3 text-xs text-gray-600">
                  Historical context changes outcomes dramatically.
                </p>
              </div>
            )}
          </div>
        </div>

        {/* Methodology Link */}
        <div className="mt-12 text-center">
          <a 
            href="/predict/methodology" 
            className="text-gray-500 hover:text-gray-300 text-sm transition"
          >
            How does this work? Read the methodology →
          </a>
        </div>
      </main>
    </div>
  );
}

// Parameter Slider Component
function ParameterSlider({ paramKey, param, value, onChange }) {
  return (
    <div>
      <div className="flex justify-between mb-1">
        <label className="text-sm text-gray-300">{param.label}</label>
        <span className="text-sm text-gray-500">{value}</span>
      </div>
      <input
        type="range"
        min={param.min}
        max={param.max}
        step={param.step}
        value={value}
        onChange={(e) => onChange(paramKey, e.target.value)}
        className="w-full h-1.5 bg-gray-700 rounded-full appearance-none cursor-pointer
                   [&::-webkit-slider-thumb]:appearance-none
                   [&::-webkit-slider-thumb]:w-3.5
                   [&::-webkit-slider-thumb]:h-3.5
                   [&::-webkit-slider-thumb]:rounded-full
                   [&::-webkit-slider-thumb]:bg-white
                   [&::-webkit-slider-thumb]:cursor-pointer
                   [&::-webkit-slider-thumb]:transition
                   [&::-webkit-slider-thumb]:hover:bg-gray-200"
      />
      <p className="text-xs text-gray-600 mt-1">{param.description}</p>
    </div>
  );
}
